.search__tabs {
    margin-bottom: 25px;
}

.search__tabs-item {
    font-weight: normal;
    line-height: 1.2;
    font-size: 16px;
    padding: 11px 26px 12px;
    color: #2f3035;
    transition: background-color .1s ease-in;
}

.search__tabs-item:hover {
    background-color: $red;
    color: $white;
}

.search__tabs-item.tab--active{
    background-color: $red;
    color: $white;
}
.search__content-item {
    display: none;
}
.search__content-item.tabs-content--active{
    display: block;
}
.search__content-form {
    display: flex;
}

.search__content-btn {
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    color: #fff;
    line-height: 1.2;
    cursor: pointer;
    font-weight: 700;
    background-color: #1c62cd;
    border-radius: 3px;
    padding: 17px 41px;
    border: none;
    margin-left: -3px;
}

.search__content-input {
    background-color: #f0f0f4;
    border-radius: 3px;
    padding: 5px 20px;
    width: 100%;
}

.search__content-input::placeholder {
    font-size: 16px;
    line-height: 1.1;
    color: #656464;
}