/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir=rtl] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Open-Sans";
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
}
.slider__item {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 50%;
  min-height: 600px;
}
.slider__wrapper {
  max-width: 90%;
  margin: 0 auto;
  text-align: center;
  color: #fff;
  padding-top: 7rem;
}
.slider__title {
  text-transform: uppercase;
  font-size: 26px;
  margin-bottom: 20px;
}
.slider__description {
  line-height: 2;
  font-size: 16px;
  margin-bottom: 60px;
}
.slider__btn {
  max-width: 300px;
  margin: 0 auto;
}

.slider__inner-btn {
  position: absolute;
  top: 30%;
  transform: translateY(-30%);
  z-index: 5;
  background-color: transparent;
  border: none;
  margin: 0;
  padding: 0;
  cursor: pointer;
}

.slider__inner-btnprev {
  left: 20px;
}

.slider__inner-btnnext {
  right: 20px;
}

.slider__dots {
  position: absolute;
  bottom: 40px;
  left: 0;
  right: 0;
  text-align: center;
}

.slider__dots li {
  display: inline-block;
  padding: 0 5px;
}

.slider__dots button {
  padding: 0;
  margin: 0;
  font-size: 0;
  width: 15px;
  height: 15px;
  border: 1px solid #fff;
  border-radius: 50%;
  cursor: pointer;
  background-color: transparent;
  transition: background-color 0.2s ease-in;
}
.slider__dots button:hover {
  background-color: #fff;
}

.slick-active button {
  background-color: #fff;
}

* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  background-color: #fff;
  color: #3B3B3B;
  font-family: "Roboto", sans-serif;
  font-size: 90%;
  min-width: 280px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-family: "Open-Sans", sans-serif;
}

.section-page {
  margin: 20px 0 20px;
}

.container {
  max-width: 1248px;
  margin: 0 auto;
  padding: 0 30px;
}

img {
  max-width: 100%;
  height: auto;
}

p {
  margin: 0 0 10px;
}

a {
  text-decoration: none;
  color: #0d6efd;
  display: inline-block;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

input {
  outline: none;
}

button {
  outline: none;
}

.title {
  text-align: center;
  text-transform: uppercase;
  color: #0d6efd;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.section-page {
  padding-bottom: 40px;
}

.footer {
  margin-top: auto;
}

.header__main {
  background-color: #3B3B3B;
  line-height: 2.5;
}
.header-top {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.header-top__list {
  display: flex;
  flex-wrap: wrap;
}
.header-top__item {
  margin-right: 35px;
}
.header-top__item:last-child {
  margin-right: 0;
}
.header-top__link {
  font-size: 14px;
  color: #fff;
}
.header__bottom {
  padding: 40px 0;
}
.header__inner {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}
.header__logo {
  margin-right: 76px;
}
.header__search {
  max-width: 100%;
  width: 454px;
  position: relative;
}
.header__form {
  max-width: 100%;
}
.header__input {
  width: 100%;
  padding: 15px 40px 15px;
  border: 0;
  background-color: #F3F3F3;
  margin-right: -35px;
  transition: box-shadow 0.2s ease-in;
}
.header__input:focus {
  box-shadow: 0 0 2px #0d6efd;
}
.header__img {
  position: absolute;
  top: 15px;
}
.header__box {
  margin-left: auto;
  margin-right: 50px;
}
.header-box__link span {
  display: block;
  color: #676F77;
}
.header-box__icons {
  display: block;
  margin: 0 auto 3px;
}
.header-box__list {
  display: flex;
  flex-wrap: wrap;
}
.header-box__item {
  padding: 0 20px 0;
}
.header__coolback {
  margin-left: auto;
}

.collback {
  display: block;
  background-color: #0d6efd;
  color: #fff;
  padding: 10px 40px 10px;
  text-transform: uppercase;
  transition: background-color 0.2s ease-in;
  text-align: center;
}
.collback:hover {
  background-color: #f00;
}

.top-menu {
  background-color: #0d6efd;
}
.top-menu__list {
  display: flex;
  flex-wrap: wrap;
}
.top-menu__item {
  margin-right: 30px;
}
.top-menu__link {
  color: #fff;
  padding: 15px 0 15px;
  font-size: 14px;
  text-transform: uppercase;
  transition: color 0.2s ease-in;
}
.top-menu__link:hover, .top-menu__link:focus, .top-menu__link.active {
  color: #ccc;
}

.section-advantages {
  background: #f2f2f2;
  padding: 30px 0 30px;
  margin-bottom: 20px;
}

.advantages__row {
  display: flex;
  flex-wrap: wrap;
}
.advantages__col {
  width: 32%;
  background: #fff;
  padding: 30px 15px;
  text-align: center;
  margin-right: 1%;
  margin-bottom: 20px;
}
.advantages__col:last-child {
  margin-right: 0;
}
.advantages__text span {
  display: block;
}
.advantages__text i {
  color: #013e98;
  font-size: 30px;
  margin-bottom: 5px;
}
.advantages__title {
  font-weight: 600;
  line-height: 1.3;
  font-size: 16px;
  margin-bottom: 5px;
}
.advantages__description {
  font-size: 14px;
}

.search__tabs {
  margin-bottom: 25px;
}

.search__tabs-item {
  font-weight: normal;
  line-height: 1.2;
  font-size: 16px;
  padding: 11px 26px 12px;
  color: #2f3035;
  transition: background-color 0.1s ease-in;
}

.search__tabs-item:hover {
  background-color: #f00;
  color: #fff;
}

.search__tabs-item.tab--active {
  background-color: #f00;
  color: #fff;
}

.search__content-item {
  display: none;
}

.search__content-item.tabs-content--active {
  display: block;
}

.search__content-form {
  display: flex;
}

.search__content-btn {
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #fff;
  line-height: 1.2;
  cursor: pointer;
  font-weight: 700;
  background-color: #1c62cd;
  border-radius: 3px;
  padding: 17px 41px;
  border: none;
  margin-left: -3px;
}

.search__content-input {
  background-color: #f0f0f4;
  border-radius: 3px;
  padding: 5px 20px;
  width: 100%;
}

.search__content-input::placeholder {
  font-size: 16px;
  line-height: 1.1;
  color: #656464;
}

.section-categories {
  background-color: #f2f2f2;
  padding: 30px 0;
}
.section-categories__title {
  margin-bottom: 20px;
}

.categories__row {
  display: flex;
  flex-wrap: wrap;
}
.categories__col {
  width: 32%;
  background-color: #fff;
  padding: 20px;
}
.categories__col:not(:last-child) {
  margin-right: 1%;
}
.categories__text {
  text-align: center;
}
.categories__text-img {
  margin-bottom: 10px;
  height: 100%;
}
.categories__text-link {
  text-transform: uppercase;
  color: #3B3B3B;
  margin-top: auto;
}