.slider {
    &__item {
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 0 50%;
        min-height: 600px;
    }
    &__wrapper {
        max-width: 90%;
        margin: 0 auto;
        text-align: center;
        color: $white;
        padding-top: 7rem;
    }
    &__title {
        text-transform: uppercase;
        font-size: 26px;
        margin-bottom: 20px;
    }
    &__description {
        line-height: 2;
        font-size: 16px;
        margin-bottom: 60px;
    }
    &__btn {
        max-width: 300px;
        margin: 0 auto;
    }
}
.slider__inner-btn {
    position: absolute;
    top: 30%;
    transform: translateY(-30%);
    z-index: 5;
    background-color: transparent;
    border: none;
    margin: 0;
    padding: 0;
    cursor: pointer;
}
.slider__inner-btnprev {
    left: 20px;
}
.slider__inner-btnnext {
    right: 20px;
}
.slider__dots {
    position: absolute;
    bottom: 40px;
    left: 0;
    right: 0;
    text-align: center;
}
.slider__dots li {
    display: inline-block;
    padding: 0 5px;
}
.slider__dots button {
    padding: 0;
    margin: 0;
    font-size: 0;
    width: 15px;
    height: 15px;
    border: 1px solid $white;
    border-radius: 50%;
    cursor: pointer;
    background-color: transparent;
    transition: background-color .2s ease-in;
    &:hover {
        background-color: $white;
    }
}
.slick-active button{
    background-color: $white;
}