.section-advantages {
    background: #f2f2f2;
    padding: 30px 0 30px;
    margin-bottom:20px;
}

.advantages {
    &__row {
        display: flex;
        flex-wrap: wrap;
    }

    &__col {
        width: 32%;
        background: #fff;
        padding: 30px 15px;
        text-align: center;
        margin-right: 1%;
        margin-bottom: 20px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__text span{
        display: block;
    }

    &__text i{
        color: #013e98;
        font-size: 30px;
        margin-bottom: 5px;
    }
    &__title {
        font-weight: 600;
        line-height: 1.3;
        font-size: 16px;
        margin-bottom: 5px;
    }
    &__description {
        font-size: 14px;
    }
}