* {
    box-sizing: border-box;
}
html,body {
    height: 100%;
}
body {
    background-color: $white;
    color: $black;
    font-family: 'Roboto', sans-serif;
    font-size: 90%;
    min-width: 280px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    padding: 0;
    font-family: 'Open-Sans', sans-serif;
}
.section-page{
   margin: 20px 0 20px;
}
.container {
    max-width: 1248px;
    margin: 0 auto;
    padding: 0 30px;
}

img {
    max-width: 100%;
    height: auto;
}

p {
    margin: 0 0 10px;
}

a {
    text-decoration: none;
    color: $blue;
    display: inline-block;
}
ul,li {
    list-style: none;
    margin: 0;
    padding: 0;
}
input {
    outline: none;
}
button {
    outline: none;
}
.title {
    text-align: center;
    text-transform: uppercase;
    color: $blue;
}
.wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
}
.section-page {
    padding-bottom: 40px;
}
.footer {
    margin-top: auto;
}

