.section-categories {
    background-color: #f2f2f2;
    padding: 30px 0;
    &__title {
        margin-bottom: 20px;
    }
}
.categories {
    &__row {
        display: flex;
        flex-wrap: wrap;
        
    }
    &__col {
       width: 32%;
       &:not(:last-child) {
           margin-right: 1%;
       }
       background-color: $white;
       padding: 20px;
    }
    &__text {
        text-align: center;
        &-img {
            margin-bottom: 10px;
            height: 100%;
        }
        &-link {
            text-transform: uppercase;
            color: $black;
            margin-top: auto;
        }
    }
}