.top-menu {
    background-color: $blue;
    &__list {
        display: flex;
        flex-wrap: wrap;
    }
    &__item {
        margin-right: 30px;
    }
    &__link {
        color: $white;
        padding: 15px 0 15px;
        font-size: 14px;
        text-transform: uppercase;
        transition: color .2s ease-in;
        &:hover,
        &:focus,
        &.active {
            color: #ccc;
        }
    }
}