.header {
    &__main {
        background-color: $black;
        line-height: 2.5;
    }

    &-top {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }

    &-top__list {
        display: flex;
        flex-wrap: wrap;
    }

    &-top__item {
        margin-right: 35px;

        &:last-child {
            margin-right: 0;
        }
    }

    &-top__link {
        font-size: 14px;
        color: $white;
    }

    &__bottom {
        padding: 40px 0;
    }

    &__inner {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: wrap;
    }

    &__logo {
        margin-right: 76px;
    }

    &__search {
        max-width: 100%;
        width: 454px;
        position: relative;
    }

    &__form {
        max-width: 100%;
    }

    &__input {
        width: 100%;
        padding: 15px 40px 15px;
        border: 0;
        background-color: #F3F3F3;
        margin-right: -35px;
        transition: box-shadow .2s ease-in;
        &:focus {
            box-shadow: 0 0 2px $blue;
        }
    }
    &__img {
        position: absolute;
        top: 15px;
    }
    &__box {
       margin-left: auto;
       margin-right: 50px;
    }
    &-box__link span {
        display: block;
        color: #676F77;
        
    }
    &-box__icons {
        display: block;
        margin: 0 auto 3px;
    }
    &-box__list {
        display: flex;
        flex-wrap: wrap;
    }
    &-box__item {
       padding: 0 20px 0;
    }
    &__coolback {
        margin-left: auto;
    }
}
.collback {
    @include button;
}